.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  font-family: $main-f;
  color: $black-text;
  font-weight: $font-weight-normal;
  overflow-x: hidden;
}

main {
  flex: 1 0 auto;
  overflow: hidden;
  width: 100%;
}

img {
  display: block;
  max-width: 100%;
  flex-shrink: 0;
}

.hero__error {
  display: block;
  margin-bottom: 10px;
  color: $error-text;
  text-align: center;
  font-size: 1.1rem;
}

.hidden {
  display: none;
}
